.mainContainer {
  // overflow: hidden
  width: 100%;
  padding-top: 80px;
  // height: 100%
  overflow-x: hidden; }

.tempHide {
  transform: translate(0, 50px);
  opacity: 0;
  @include dTime(.8s);

  &.left {
    transform: translate(-50px, 0px); }
  &.right {
    transform: translate(50px, 0px); }
  &.show {
    opacity: 1;
    transform: translate(0, 0); } }


.underline {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #fff;
    bottom: -1px;
    left: 0; } }

@mixin aniPicsWrap {
  position: relative;
  > * {
    position: absolute;
    top: 0;
    left: 0; }

  > *:first-child {
    position: relative; } }

@mixin sunAni {
  animation-name: sun;
  animation-duration: .1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out; }

@keyframes sun {
  from {
    opacity: 1; }
  50% {
    opacity: 0.95; }
  to {
    opacity: 1; } }

@mixin addSun {
  position: relative;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: -50px;
    right: -250px;
    @include bg('sunlights.png'); } }




#top-btn {
  @include bg('topBtn.png');
  position: fixed;
  bottom: 100px;
  right: 20px;
  cursor: pointer;
  z-index: 99;
  transform: translate(0, 500px);
  will-change: transform;
  opacity: 0;
  @include dTime(.5s);

  &.show {
    opacity: 1;
    transform: translate(0, 0px);
    &:hover {
      transform: translate(0, -10px); } }
  @include phone {
    right: 0px; } }

